body {
  background-color: #f8f9fa;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  background: #fff;
  border-radius: 8px;
  max-width: 600px;
  margin: auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

header {
  text-align: center;
  background-color: #fff;
  padding: 20px 0;
  position: relative;
  box-shadow: 0 2px 5px #0000001a;
}

.logout-btn {
  color: #fff;
  cursor: pointer;
  background-color: #dc3545;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.logout-btn:hover {
  background-color: #c82333;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
}

button:hover {
  background-color: #0056b3;
}

footer {
  text-align: center;
  background-color: #fff;
  margin-top: auto;
  padding: 10px;
  box-shadow: 0 -2px 5px #0000001a;
}

#chatbot-container {
  background: #fff;
  border: 1px solid #ccc;
  width: 300px;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 200px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

#chat-input {
  width: calc(100% - 60px);
  margin-right: 5px;
  padding: 5px;
}

#send-btn {
  cursor: pointer;
  padding: 5px 10px;
}

.history {
  padding-bottom: 15px;
}
